<script>
import { ref, computed, onMounted, reactive } from 'vue'
import { useStore } from 'vuex'
import { Search, Sort, Files, Setting, ArrowDown, More } from '@element-plus/icons-vue'
import { ElMessage, ElMessageBox } from 'element-plus'

export default {
  name: 'FBXView',
  components: {
    Search,
    Sort,
    Files,
    Setting,
    ArrowDown,
    More
  },
  setup() {
    const store = useStore()
    
    // 
    const activeTab = ref('all')
    
    // 
    const dateType = ref('eta')
    const dateRange = ref([])
    
    // 
    const searchType = ref('containerNo')
    const searchNumber = ref('')
    
    // 
    const fuzzySearchText = ref('')
    
    // 
    const tableData = computed(() => store.getters.paginatedData)
    const total = computed(() => store.state.total)
    
    // 
    const currentPage = ref(1)
    const pageSize = ref(20)
    const paginationInfo = computed(() => {
      const startRow = (currentPage.value - 1) * pageSize.value + 1
      const endRow = Math.min(startRow + pageSize.value - 1, total.value)
      return {
        startRow,
        endRow
      }
    })
    
    // 
    const selectedRows = ref([])
    
    // 
    const filterModalVisible = ref(false)
    const sortModalVisible = ref(false)
    const groupModalVisible = ref(false)
    const fieldModalVisible = ref(false)
    
    // 
    const filters = ref([])
    
    // 
    const sortSettings = ref([])
    
    // 
    const groupSettings = reactive({
      level1: '',
      level2: '',
      level3: ''
    })
    
    // 
    const allFields = [
      { label: '', value: 'containerNo' },
      { label: '', value: 'waybillNo' },
      { label: '', value: 'customerService' },
      { label: '', value: 'originalAddress' },
      { label: '', value: 'address' },
      { label: 'City', value: 'city' },
      { label: 'State', value: 'state' },
      { label: 'Zip_Code', value: 'zipCode' },
      { label: '', value: 'email' },
      { label: '', value: 'phone' },
      { label: '', value: 'deliveryArea' },
      { label: '', value: 'warehouseCode' },
      { label: '', value: 'channel' },
      { label: '', value: 'boxCount' },
      { label: '', value: 'weight' },
      { label: '', value: 'volume' },
      { label: '', value: 'palletCount' },
      { label: '', value: 'location' },
      { label: 'ETA', value: 'eta' },
      { label: '', value: 'pickupTime' },
      { label: '', value: 'arrivalTime' },
      { label: '', value: 'expectedUnloadingTime' },
      { label: '', value: 'unloadingTime' },
      { label: '', value: 'appointmentNo' },
      { label: '', value: 'deliveryAppointmentTime' },
      { label: 'PC', value: 'pcNo' },
      { label: '', value: 'status' },
      { label: '', value: 'tags' },
      { label: '', value: 'followUpRecord' },
      { label: '', value: 'memo' },
      { label: '', value: 'internalNote' },
      { label: '()', value: 'lianyuPrice' },
      { label: '', value: 'shippingMark' },
      { label: '', value: 'inquiryPrice' }
    ]
    const selectedFields = ref(allFields.map(field => field.value))
    
    // 
    const fieldOptions = computed(() => {
      return allFields.filter(field => selectedFields.value.includes(field.value))
    })
    
    // 
    const groupableFields = computed(() => {
      return [
        { label: '', value: 'containerNo' },
        { label: '', value: 'waybillNo' },
        { label: '', value: 'customerService' },
        { label: '', value: 'originalAddress' },
        { label: '', value: 'address' },
        { label: 'City', value: 'city' },
        { label: 'State', value: 'state' },
        { label: 'Zip_Code', value: 'zipCode' },
        { label: '', value: 'email' },
        { label: '', value: 'phone' },
        { label: '', value: 'deliveryArea' },
        { label: '', value: 'warehouseCode' },
        { label: '', value: 'channel' },
        { label: '', value: 'boxCount' },
        { label: '', value: 'weight' },
        { label: '', value: 'volume' },
        { label: '', value: 'palletCount' },
        { label: '', value: 'location' },
        { label: 'ETA', value: 'eta' },
        { label: '', value: 'pickupTime' },
        { label: '', value: 'arrivalTime' },
        { label: '', value: 'expectedUnloadingTime' },
        { label: '', value: 'unloadingTime' },
        { label: '', value: 'appointmentNo' },
        { label: '', value: 'deliveryAppointmentTime' },
        { label: 'PC', value: 'pcNo' },
        { label: '', value: 'status' },
        { label: '', value: 'tags' },
        { label: '', value: 'followUpRecord' },
        { label: '', value: 'memo' },
        { label: '', value: 'internalNote' },
        { label: '()', value: 'lianyuPrice' },
        { label: '', value: 'shippingMark' },
        { label: '', value: 'inquiryPrice' }
      ]
    })
    
    // 
    const views = ref([
      { id: 'all', name: '', count: 0 },
      { id: 'pending', name: '', count: 0 },
      { id: 'appointed', name: '', count: 0 }
    ])
    const viewDialogVisible = ref(false)
    const viewDialogType = ref('add')
    const viewForm = reactive({
      name: '',
      description: '',
      filters: [],
      sortSettings: [],
      groupSettings: {},
      selectedFields: []
    })

    // 
    const batchDialogVisible = ref(false)
    const batchOperation = ref('')
    const batchForm = reactive({
      followUp: '',
      memo: '',
      pcType: 'delivery',
      targetWarehouse: ''
    })
    const warehouseOptions = [
      { label: 'A', value: 'A' },
      { label: 'B', value: 'B' },
      { label: 'C', value: 'C' }
    ]

    // 
    const batchDialogTitle = computed(() => {
      switch (batchOperation.value) {
        case 'followUp':
          return ''
        case 'memo':
          return 'MEMO'
        case 'createPC':
          return 'PC'
        default:
          return ''
      }
    })

    // 
    const statusOptions = [
      { label: '', value: 'pending' },
      { label: '', value: 'processing' },
      { label: '', value: 'shipped' },
      { label: '', value: 'delivered' },
      { label: '', value: 'cancelled' }
    ]

    // 
    const availableTags = ['', '', '', '', '']

    // 
    const hasGroupedData = computed(() => {
      return tableData.value.some(item => item.isGroup);
    });

    // 
    const getGroupTagType = (level) => {
      const types = ['', 'primary', 'success', 'warning'];
      return types[level] || 'info';
    };

    // 
    const getRowClassName = ({ row }) => {
      if (row.isGroup) {
        return row.groupClass || '';
      }
      if (row.dataParentGroupKey) {
        // 
        return `data-row data-parent-group-key-${row.dataParentGroupKey.replace(/[^a-zA-Z0-9]/g, '-')}`;
      }
      return '';
    }

    // 
    onMounted(() => {
      const today = new Date()
      const oneMonthAgo = new Date()
      oneMonthAgo.setMonth(today.getMonth() - 1)
      dateRange.value = [oneMonthAgo, today]
      
      store.dispatch('loadTableData')
      loadViewsFromStorage()
    })
    
    // 
    const handleSearch = () => {
      const searchParams = {
        dateType: dateType.value,
        dateRange: dateRange.value,
        searchType: searchType.value,
        searchNumber: searchNumber.value,
        fuzzySearchText: fuzzySearchText.value
      }
      
      store.dispatch('searchData', searchParams)
    }
    
    const handleReset = () => {
      dateType.value = 'eta'
      const today = new Date()
      const oneMonthAgo = new Date()
      oneMonthAgo.setMonth(today.getMonth() - 1)
      dateRange.value = [oneMonthAgo, today]
      searchType.value = 'containerNo'
      searchNumber.value = ''
      fuzzySearchText.value = ''
      
      handleSearch()
    }
    
    // 
    const handleSelectionChange = (rows) => {
      selectedRows.value = rows
    }
    
    // 
    const handleCurrentChange = (page) => {
      currentPage.value = page
      store.commit('SET_CURRENT_PAGE', page)
    }
    
    const handleSizeChange = (size) => {
      pageSize.value = size
      store.commit('SET_PAGE_SIZE', size)
    }
    
    // 
    const showFilterModal = () => {
      filterModalVisible.value = true
    }
    
    const addFilter = () => {
      filters.value.push({
        field: '',
        operator: 'eq',
        value: ''
      })
    }
    
    const removeFilter = (index) => {
      filters.value.splice(index, 1)
    }
    
    const applyFilters = () => {
      store.commit('SET_FILTERS', filters.value)
      store.dispatch('applyFilters')
      filterModalVisible.value = false
    }
    
    // 
    const showSortModal = () => {
      sortModalVisible.value = true
    }
    
    const addSort = () => {
      sortSettings.value.push({
        field: '',
        direction: 'asc'
      })
    }
    
    const removeSort = (index) => {
      sortSettings.value.splice(index, 1)
    }
    
    const applySortSettings = () => {
      store.commit('SET_SORT_SETTINGS', sortSettings.value)
      store.dispatch('applySort')
      sortModalVisible.value = false
    }
    
    // 
    const showGroupModal = () => {
      groupModalVisible.value = true
    }
    
    const applyGroupSettings = () => {
      const groupFields = [];
      if (groupSettings.level1) groupFields.push(groupSettings.level1);
      if (groupSettings.level2) groupFields.push(groupSettings.level2);
      if (groupSettings.level3) groupFields.push(groupSettings.level3);
      
      store.dispatch('applyGroup', { groupFields });
      groupModalVisible.value = false;
    }
    
    // 
    const showFieldModal = () => {
      fieldModalVisible.value = true
    }
    
    const resetFieldSettings = () => {
      selectedFields.value = allFields.map(field => field.value)
    }
    
    const applyFieldSettings = () => {
      store.commit('SET_FIELD_SETTINGS', selectedFields.value)
      store.dispatch('applyFieldSettings')
      fieldModalVisible.value = false
    }

    // 
    const cellEditDialogVisible = ref(false)
    const cellDetailDialogVisible = ref(false)
    const editingCell = reactive({
      row: null,
      field: '',
      value: '',
      title: '',
      type: 'text',
      options: [],
      placeholder: ''
    })
    const cellDetailData = reactive({
      currentValue: '',
      historyValues: []
    })

    // 
    const editableFields = {
      address: { type: 'text', title: '', placeholder: '' },
      city: { type: 'text', title: 'City', placeholder: '' },
      state: { type: 'text', title: 'State', placeholder: '/' },
      zipCode: { type: 'text', title: 'Zip_Code', placeholder: '' },
      email: { type: 'email', title: '', placeholder: '' },
      phone: { type: 'text', title: '', placeholder: '' },
      deliveryArea: { type: 'text', title: '', placeholder: '' },
      appointmentNo: { type: 'text', title: '', placeholder: '' },
      deliveryAppointmentTime: { type: 'datetime', title: '', placeholder: '' },
      status: { 
        type: 'tags', 
        title: '',
        options: [
          { label: '', value: 'appointed' },
          { label: '', value: 'pending' },
          { label: '', value: 'phone' },
          { label: '', value: 'email' },
          { label: '', value: 'platform' }
        ]
      },
      tags: { 
        type: 'tags', 
        title: '',
        options: ['', '', '', '', '']
      },
      followUpRecord: { type: 'textarea', title: '', placeholder: '' },
      memo: { type: 'textarea', title: '', placeholder: '' },
      internalNote: { type: 'textarea', title: '', placeholder: '' },
      lianyuPrice: { type: 'text', title: '()', placeholder: '' }
    }

    // 
    const handleCellClick = (row, field) => {
      const fieldConfig = editableFields[field]
      if (!fieldConfig) return
      
      // 
      row.editing = field
    }

    // 
    const handleCellDblClick = async (row, field) => {
      const fieldConfig = editableFields[field]
      if (!fieldConfig) return

      editingCell.row = row
      editingCell.field = field
      editingCell.title = fieldConfig.title
      editingCell.type = fieldConfig.type
      editingCell.value = row[field]
      editingCell.options = fieldConfig.options || []
      
      // textarea
      if (['address', 'memo', 'followUpRecord'].includes(field)) {
        editingCell.type = 'textarea'
      }
      
      cellEditDialogVisible.value = true
    }

    // 
    const handleCellEditConfirm = async (row, field) => {
      if (!row || !field) return

      try {
        await store.dispatch('updateField', {
          id: row.id,
          field: field,
          value: row[field]
        })
        
        // 
        row.editing = null
        ElMessage.success('')
      } catch (error) {
        ElMessage.error('')
      }
    }

    // 
    const handleDialogEditConfirm = async () => {
      if (!editingCell.row || !editingCell.field) return

      try {
        await store.dispatch('updateField', {
          id: editingCell.row.id,
          field: editingCell.field,
          value: editingCell.value
        })
        
        // 
        editingCell.row[editingCell.field] = editingCell.value
        
        // 
        cellEditDialogVisible.value = false
        ElMessage.success('')
      } catch (error) {
        ElMessage.error('')
      }
    }

    // 
    const handleTabClick = (tab) => {
      if (tab.props.name === 'add') {
        handleAddView()
      }
    }

    const handleAddView = () => {
      viewDialogType.value = 'add'
      viewForm.name = ''
      viewForm.description = ''
      viewDialogVisible.value = true
    }

    // 
    const handleCommand = (command, row) => {
      let addressInfo = ''
      
      switch (command) {
        case 'palletDetail':
          // 
          break
        case 'copyAddress':
          // 
          addressInfo = `${row.address}, ${row.city}, ${row.state} ${row.zipCode}`
          navigator.clipboard.writeText(addressInfo)
            .then(() => {
              ElMessage.success('')
            })
            .catch(err => {
              console.error(':', err)
              ElMessage.error('')
            })
          break
        case 'followUp':
          // 
          break
        case 'memo':
          // MEMO
          break
        case 'markException':
          // 
          break
        case 'history':
          // 
          break
        case 'detail':
          // 
          break
        case 'cost':
          // 
          break
      }
    }

    // 
    const handleViewCommand = (command, view) => {
      switch (command) {
        case 'copy':
          viewDialogType.value = 'copy'
          viewForm.name = `${view.name} `
          viewForm.description = ''
          viewDialogVisible.value = true
          break
        case 'rename':
          viewDialogType.value = 'rename'
          viewForm.name = view.name
          viewForm.description = view.description || ''
          viewDialogVisible.value = true
          break
        case 'delete':
          ElMessageBox.confirm(
            '',
            '',
            {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }
          ).then(() => {
            // 
            const index = views.value.findIndex(v => v.id === view.id)
            if (index > -1) {
              views.value.splice(index, 1)
              // 
              saveViewsToStorage()
            }
          })
          break
      }
    }

    const handleViewSubmit = () => {
      if (!viewForm.name) {
        ElMessage.warning('')
        return
      }

      const newView = {
        id: Date.now().toString(),
        name: viewForm.name,
        description: viewForm.description,
        filters: [...filters.value],
        sortSettings: [...sortSettings.value],
        groupSettings: { ...groupSettings },
        selectedFields: [...selectedFields.value],
        count: 0
      }

      if (viewDialogType.value === 'add' || viewDialogType.value === 'copy') {
        views.value.push(newView)
      } else if (viewDialogType.value === 'rename') {
        const index = views.value.findIndex(v => v.id === activeTab.value)
        if (index > -1) {
          views.value[index] = { ...views.value[index], ...newView }
        }
      }

      // 
      saveViewsToStorage()
      viewDialogVisible.value = false
    }

    const handleSaveView = () => {
      const currentView = views.value.find(v => v.id === activeTab.value)
      if (!currentView) return

      currentView.filters = [...filters.value]
      currentView.sortSettings = [...sortSettings.value]
      currentView.groupSettings = { ...groupSettings }
      currentView.selectedFields = [...selectedFields.value]

      // 
      saveViewsToStorage()
      ElMessage.success('')
    }

    // 
    const handleBatchOperation = (operation) => {
      if (selectedRows.value.length === 0) {
        ElMessage.warning('')
        return
      }

      batchOperation.value = operation
      batchForm.followUp = ''
      batchForm.memo = ''
      batchForm.pcType = 'delivery'
      batchForm.targetWarehouse = ''
      batchDialogVisible.value = true
    }

    const handleBatchSubmit = () => {
      // 
      switch (batchOperation.value) {
        case 'followUp':
          if (!batchForm.followUp) {
            ElMessage.warning('')
            return
          }
          // 
          break
        case 'memo':
          if (!batchForm.memo) {
            ElMessage.warning('MEMO')
            return
          }
          // MEMO
          break
        case 'createPC':
          if (batchForm.pcType === 'transfer' && !batchForm.targetWarehouse) {
            ElMessage.warning('')
            return
          }
          // PC
          break
      }

      batchDialogVisible.value = false
      ElMessage.success('')
    }

    // 
    const handleExport = () => {
      // 
      console.log('', {
        filters: filters.value,
        sortSettings: sortSettings.value
      })
    }

    // 
    const saveViewsToStorage = () => {
      localStorage.setItem('fbx-views', JSON.stringify(views.value))
    }

    const loadViewsFromStorage = () => {
      const savedViews = localStorage.getItem('fbx-views')
      if (savedViews) {
        views.value = JSON.parse(savedViews)
      }
    }

    // 
    const getStatusType = (status) => {
      const statusMap = {
        pending: 'info',
        appointed: 'success',
        phone: 'warning',
        email: 'warning',
        platform: 'warning',
        completed: 'success',
        cancelled: 'danger'
      }
      return statusMap[status] || 'info'
    }

    const getStatusText = (status) => {
      const statusMap = {
        pending: '',
        appointed: '',
        phone: '',
        email: '',
        platform: '',
        completed: '',
        cancelled: ''
      }
      return statusMap[status] || status
    }

    // 
    const vFocus = {
      mounted: (el) => {
        //  el-select 
        const input = el.querySelector('input')
        if (input) {
          input.focus()
        } else {
          el.focus()
        }
      }
    }

    // 
    const handleTagClose = (row, field, tag) => {
      if (!row[field]) return
      
      const index = row[field].indexOf(tag)
      if (index > -1) {
        const newValues = [...row[field]]
        newValues.splice(index, 1)
        row[field] = newValues
        handleCellEditConfirm(row, field)
      }
    }

    return {
      activeTab,
      dateType,
      dateRange,
      searchType,
      searchNumber,
      fuzzySearchText,
      tableData,
      total,
      currentPage,
      pageSize,
      paginationInfo,
      selectedRows,
      filterModalVisible,
      sortModalVisible,
      groupModalVisible,
      fieldModalVisible,
      filters,
      sortSettings,
      groupSettings,
      allFields,
      selectedFields,
      fieldOptions,
      groupableFields,
      handleSearch,
      handleReset,
      handleSelectionChange,
      handleCurrentChange,
      handleSizeChange,
      showFilterModal,
      addFilter,
      removeFilter,
      applyFilters,
      showSortModal,
      addSort,
      removeSort,
      applySortSettings,
      showGroupModal,
      applyGroupSettings,
      showFieldModal,
      resetFieldSettings,
      applyFieldSettings,
      cellEditDialogVisible,
      cellDetailDialogVisible,
      editingCell,
      cellDetailData,
      statusOptions,
      availableTags,
      handleCellClick,
      handleCellDblClick,
      handleCellEditConfirm,
      handleDialogEditConfirm,
      handleCommand,
      views,
      viewDialogVisible,
      viewDialogType,
      viewForm,
      batchDialogVisible,
      batchOperation,
      batchForm,
      batchDialogTitle,
      warehouseOptions,
      handleTabClick,
      handleAddView,
      handleViewCommand,
      handleViewSubmit,
      handleSaveView,
      handleBatchOperation,
      handleBatchSubmit,
      handleExport,
      getStatusType,
      getStatusText,
      vFocus,
      handleTagClose,
      hasGroupedData,
      getGroupTagType,
      getRowClassName
    }
  }
}
</script>

